<template>
	<section class="wrap has_header_wrap">
		<section class="wrap_in">
			<!-- 头部标题 -->
			<com-header titles="罕见病频道">
				<template  slot="style-def"></template>
			</com-header>
			<!-- 头部标题 end -->
			
			<!-- banner -->
			<div class="banner-2" v-for="(bn,index) in banners">
				<div class="banner-2-in">
					<a :href="bn.advertisementUrl">
						<img :src="bn.advertisementPhoto">
					</a>
					<!-- 返回字段无此数据 
					<div class="d_flex">
						<h3>瘦身爆款 买二送一原品</h3>
						<p>舒尔佳 奥利司他胶囊</p>
					</div> -->
				</div>
			</div>
			<!-- banner end -->
			
			<!-- 产品列表 -->
			<div class="pros" v-if="recomList.length">
				<ul class="d_flex">
					<li 
						v-for="(rl,index) in recomList"
						@click="jumpDetail(rl,index)">
						<img :src="rl | photoFilter" :alt="rl?rl.productName:''">
						<h3>{{ rl?rl.productName:'' }}</h3>
						<span>{{ rl?rl.spec:'' }}</span>
						<h2 ><span class="r_color">{{ rl?rl.ourPrice:'' | priceFilter }}</span></h2>
						<!-- 暂时不需要 
						<div class="activitys">
							<span class="inlie_block">套餐</span> 
							<span class="inlie_block">秒杀</span> 
							<span class="inlie_block">满减</span>
						</div> -->
					</li>
				</ul>
			</div>			
			<!-- 产品列表 end -->
		</section>

		<!-- 子页面 -->
	    <transition name="fade">
	    	<router-view v-on:changeClass="changeClassOne"></router-view>
	    </transition>
	    <!-- 子页面 end -->
	</section>
</template>
<script>
import { Header, Button, Badge } from 'mint-ui';
import comHeaderTitle from '@/components/Common/ComHeaderTitle'

export default {
	name: 'myinfo',
	data() {
		return {
	      isUnscRollable: false, //是否页面不可滚动
	      backParentPage: false, //是否回到父页面
	      banners: [],//轮播图
	      recomList: [],//商品推荐
		}
	},
	props: [
		// 'pclass',
	],
	filters :{
		//图片地址筛选
		photoFilter ( data ){
			if(data.productPhotos.length <= 0){
				return './static/images/ak.png';
			}else{
				return data.productPhotos[0].photoURL;
			}
		},
		//价格过滤
		priceFilter ( data ){
			if( data || data === 0 ){
				return '￥' + Number(data).toFixed(2);
			}else{
				return '';
			}
		},
	},
	components: {
		'mt-header': Header,
		'mt-button': Button,
		'mt-badge':  Badge,
		comHeaderTitle, // 公共头部标题
	},
	created : function(){
		const _self = this;
	    var myDate = new Date();//获取系统当前时间
	    var y = myDate.getFullYear();
	    var m = myDate.getMonth() + 1;
	    if( m < 10 ){
	      m = '0'+m;
	    }
	    var d = myDate.getDate();
	    if( d < 10 ){
	      d = '0'+d;
	    }
	    var datas = y + '-' + m + '-' + d + ' ' + '23:59:59';

	    //轮播图模块
	    this.$ajax({
	      url: _self.webRoot + '/seachAd',
	      // url: '/seachAd',
	      data: {
	        endDate: datas,
	        pageNo: 'channelHjBanner',
	        isValid: 1
	      }
	    })
	    .then(function(res){
	      console.log('channelMaBanner: ',res)
	      if( res.data.success ){
	        _self.banners = res.data.data;
	      }else{
	        _self.banners = '';
	        console.log('banner模块不存在')
	      }
	    })
	    .catch(function(err){
	      console.log(err)
	    });

	    //产品推荐
	    this.$options.methods.getProRecom( _self, datas, 'channelHjTj', datas, _self.recomList, '商品推荐模块无数据' );
	},
	mounted : function() {
		const _self = this;
		this.$emit('changeClass',false);
	},
	updated (){
	    this.$nextTick(function () {
	      if( !this.backParentPage ){
	        // this.isUnscRollable = true;
	      }
	    }) 
	},
	destroyed (){
		this.$emit('changeClass',true);
	},
  	methods: {

  		//跳转到详情页
  		jumpDetail ( item,index ){
  			this.$router.push({path: '/product_detail', query: {proid: item.productID}});
  		},
  		// 获取模块数据
	    getProRecom ( _self, endDate1, pageNo, endDate2, moduleData, errMsg ){
	      _self.$ajax({
	        url: _self.webRoot + '/seachSt',
	        // url: '/seachSt',
	        data: {
	          endDate: endDate1,
	          pageNo: pageNo,
	          isValid: 1
	        }
	      })
	      .then(function(res){
	      	if(!res.data.data[0]){return}
	        var showId = res.data.data[0].showID;

	        if( res.data.success ){
	          _self.$ajax({
	            url: _self.webRoot + '/seachStDetail',
	            // url: '/seachStDetail',
	            data: {
	              endDate: endDate2,
	              showID: showId,
	              isValid: 1
	            }
	          })
	          .then(function(res){
	            console.log(pageNo, moduleData)
	            if( res.data.success ){
	              moduleData.splice(0); //先移除数据
	              var productIdList = [];

	              for( let x in res.data.data ){
	                productIdList.push( res.data.data[x].productID );
	              }

	              _self.$ajax({
	                url: _self.webRoot + '/product/getProductByProductNumber',
	                data: JSON.stringify({
	                  memberId: _self.$store.state.memberId,
	                  productNumbers: productIdList,
	                }),
	                headers: {'Content-Type' : 'application/json;charset=utf-8'},
	                transformRequest: [function (data) {
	                    return data
	                }]
	              })
	              .then(function(res){
	                console.log('productNumbers: ', res)
	                if( res.data.success && res.data.data.length>0 ){
	                  for( let x in res.data.data ){
	                    moduleData.push(res.data.data[x])
	                  }
	                }
	                if( !res.data.success ){
	                  moduleData = [];
	                  console.log(errMsg)
	                }
	                console.log('akRecommend', moduleData)
	              })
	              .catch(function(err){
	                console.log(err)
	              });
	            }else{
	              moduleData = [];
	              console.log(errMsg)
	            }
	          })
	          .catch(function(err){
	            console.log(err)
	          });
	        }else{
	          moduleData = [];
	          console.log(errMsg)
	        }
	      })
	      .catch(function(err){
	        console.log(err)
	      });
	    },
	    // 判断是否返回父页面
	    changeClassOne ( isChildDestroyed ){
	      if( isChildDestroyed ){ 
	        //退出子页面
	        this.backParentPage = true; //返回首页
	        this.isUnscRollable = false;
	      }else{
	        //没退出子页面
	        this.backParentPage = false; // 没返回首页
	        this.isUnscRollable = true;
	      }
	    },
  	}
}
</script>

<style scoped>
	@import './Css/channel.css';
</style>


